export default {
    name: "detail",
    data() {
        return {
            detail: null,
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        const self = this;
        this.$showLoading({title: `加载中`});
        self.$request({
            url: self.$api.balance.log_detail,
            data: {
                id: options.id,
            }
        }).then((info) => {
            if (info.code === 0) {
                this.detail = info.data.detail;
            }
            this.$hideLoading();
        }).catch((info) => {
            this.$hideLoading();
        })
    },
}
